import { Rectangle, Context, IDrawable } from "../Drawing/Graphics";
import { Mouse } from "../Input/input.module";
import { Spawner } from "../Spawn";
import {IUpdateable} from "../IUpdateable";

export abstract class GameObject extends Rectangle implements IDrawable, IUpdateable{
    public isMouseOver: boolean;
    public isMouseDown: boolean;
    public isDragged : boolean;


    constructor(x: number,
        y: number,
        width: number,
        height: number) {
        super(x, y, width, height);
    }

    public abstract update(): void;

    public abstract draw(context: Context): void;
}